@import "../../../node_modules/slick-carousel/slick/slick.scss";
.slick-list {
    overflow: hidden;
    padding-bottom: 80px;
}

.slick-track{
    display: flex;
   margin: 0 auto;
}
.galary-icecream__list{
    position: relative;
    & .slick-list{
        position: relative;
    top: -25px;
    }
}
.galary-icecream__list .slick-arrow {
    position: absolute;
    top:80%;
    z-index: 10;
    font-size: 0;
    width: 60px;
    height: 60px;
    background-size: cover;
    background-position: center;
    border: none;

}

.galary-icecream__list .slick-prev.slick-arrow {
    left: 0;
    background: url(/src/images/galary&customers/forward.svg) no-repeat;
    background-size: cover;
    background-position: center;
}

.galary-icecream__list .slick-next.slick-arrow {
    right: 0;
    background: url(/src/images/galary&customers/reply.svg) no-repeat;
    background-size: cover;
    background-position: center;
}


.galary-icecream__list .slick-dots {
    display: flex;
    justify-content: center;

    & li:not(:last-child) {
        margin-right: 10px;
    }
        & button {
            font-size: 0;
            width: 10px;
            height: 10px;
            padding: 0;
            border-radius: 60%;
            border-color: aqua;
            background-color: violet;
        }
                & .slick-active button {
                    background-color: black;
                    transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
                }
}

@media screen and (min-width:768px) {
    .galary-icecream__list .slick-arrow {
        top: 60%;
    }
        .galary-icecream__list .slick-list{
            top: -164px;
        }
 .galary-icecream__list .slick-prev.slick-arrow {
     left: 20%;
 }

 .galary-icecream__list .slick-next.slick-arrow {
     right: 20%;
 }
        }
@media screen and (min-width:1200px) {
.galary-icecream__list .slick-prev.slick-arrow {
left: 20%;
}   
.galary-icecream__list .slick-next.slick-arrow {
right: 20%;
}
.galary-icecream__list .slick-list {
   top: -94px;
   }
.galary-icecream__list .slick-arrow {
              top: 70%;
}
 }


.galary-icecream__list .slick-dots .slick-active button{
    background-color: black;
    transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
